/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 100%;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0;
  /* 1 */
  -ms-interpolation-mode: bicubic;
  /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  white-space: normal;
  /* 2 */
  *margin-left: -7px;
  /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%;
  /* 1 */
  margin: 0;
  /* 2 */
  vertical-align: baseline;
  /* 3 */
  *vertical-align: middle;
  /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

* {
  box-sizing: border-box;
}

body {
  background-color: white;
  color: black;
  font-family: "Open Sans", sans-serif;
}

html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 15px;
}
@media screen and (min-width: 1352px) {
  .container {
    width: 1352px;
  }
}

.logo {
  display: inline-block;
  text-align: left;
  text-decoration: none;
  font-size: 3.2rem;
  font-family: "Raleway", sans-serif;
  color: #08a6e4;
  font-weight: 300;
  padding: 10px 10px 0 10px;
}
.logo--footer {
  font-size: 2.6rem;
}

h1, h2, h3, h4 {
  font-family: "Raleway", sans-serif;
  color: black;
  font-weight: 400;
}

.opening__heading {
  font-size: 3.5rem;
}

.feature--heading {
  font-size: 3rem;
}

.pricing--heading {
  font-size: 3.6rem;
}

.pricing--title {
  font-size: 2rem;
  font-weight: 800;
}

.pricing--offer {
  font-size: 1.2rem;
  font-weight: 800;
}

.btn {
  color: white;
  font-weight: 800;
  display: inline-block;
  padding: 16px 57px;
  border-radius: 23px;
  border: none;
}

.btn--primary {
  background-color: #08a6e4;
  box-shadow: 5px 10px 14px rgba(7, 172, 230, 0.16);
}

.btn--large {
  padding: 22px 92px;
  font-size: 1.6rem;
  border-radius: 40px;
}

.header {
  background-color: white;
  height: 70px;
  box-shadow: 0px 3px 6px #00000029;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header__nav {
  display: flex;
}
.header__nav .nav__checkbox {
  display: none;
  opacity: 0;
  margin: 0;
  position: absolute;
}
@media screen and (min-width: 820px) {
  .header__nav .nav__checkbox {
    display: none;
    top: -9999px;
    left: -9999px;
  }
}
.header__nav .nav__checkbox:checked ~ .nav__list {
  max-height: 300px;
  overflow: visible;
}
.header__nav .nav__checkbox:checked ~ .nav__menu .burger {
  z-index: -1;
}
.header__nav .nav__checkbox:checked ~ .nav__menu .times {
  z-index: 1;
}
.header__nav .nav__menu {
  font-size: 2.6rem;
  position: relative;
  padding: 15px;
  background-color: white;
}
.header__nav .nav__menu .burger {
  position: absolute;
  top: 3px;
  right: 10px;
  color: #08a6e4;
}
@media screen and (min-width: 820px) {
  .header__nav .nav__menu .burger {
    display: none;
    top: -9999px;
    left: -9999px;
  }
}
.header__nav .nav__menu .times {
  position: absolute;
  top: 3px;
  right: 20px;
  color: #08a6e4;
  z-index: -1;
}
@media screen and (min-width: 820px) {
  .header__nav .nav__menu .times {
    display: none;
    top: -9999px;
    left: -9999px;
  }
}
.header__nav .nav__list {
  list-style: none;
  position: absolute;
  top: 70px;
  left: 0;
  background-color: white;
  box-shadow: 0px 3px 6px #00000029;
  width: 100%;
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
@media screen and (min-width: 820px) {
  .header__nav .nav__list {
    max-height: unset;
    position: static;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: none;
    margin: 0;
  }
}
.header__nav .nav__element {
  padding: 14px 26px 0;
}
.header__nav .nav__element:first-child {
  padding-top: 35px;
}
.header__nav .nav__element:last-child {
  padding-bottom: 35px;
}
@media screen and (min-width: 820px) {
  .header__nav .nav__element:first-child {
    padding-top: 14px;
  }
  .header__nav .nav__element:last-child {
    padding-bottom: 0;
  }
}
.header__nav .nav__link {
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.328rem;
  font-size: 1.1rem;
  color: #a5a5a5;
  border-bottom: 4px solid transparent;
  display: flex;
  flex-grow: 1;
  white-space: nowrap;
}
.header__nav .nav__link:hover {
  font-weight: 900;
  color: black;
  cursor: pointer;
}
@media screen and (min-width: 820px) {
  .header__nav .nav__link:hover {
    border-bottom: 4px solid #08a6e4;
    padding: 25px 0;
  }
}

.opening {
  background-color: white;
}
.opening__wrapper {
  margin: 0 auto;
  text-align: center;
  height: 100%;
}
.opening__heading {
  margin-top: 100px;
}
@media screen and (min-width: 820px) {
  .opening__heading {
    margin-top: 130px;
  }
}
.opening__paragraph {
  margin: 20px 0 28px 0;
  display: flex;
  flex-wrap: wrap;
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  justify-content: center;
}
.opening__image {
  margin: 60px 0;
}
.opening img {
  width: 100%;
  height: auto;
  clip-path: inset(0 10px 0 10px);
}
@media screen and (min-width: 820px) {
  .opening .btn--large {
    padding-right: 157px;
    padding-left: 157px;
  }
}